<script>
/* TYPE SCRIPT */
export default {
  data: () => {
    return {
      typeValue: "",
      typeStatus: false,
      typeArray: [
        "Multimediedesigner",
        "Grafisk Design",
        "Video/Foto Redigering",
        "Brugeroplevelse (User Experience)",
      ],
      typingSpeed: 75,
      erasingSpeed: 40,
      newTextDelay: 2000,
      typeIndex: 0,
      charIndex: 0,
    };
  },
  methods: {
    typeText() {
      if (this.charIndex < this.typeArray[this.typeIndex].length) {
        if (!this.typeStatus) this.typeStatus = true;

        this.typeValue += this.typeArray[this.typeIndex].charAt(this.charIndex);
        this.charIndex += 1;

        setTimeout(this.typeText, this.typingSpeed);
      } else {
        this.typeStatus = false;
        setTimeout(this.eraseText, this.newTextDelay);
      }
    },
    eraseText() {
      if (this.charIndex > 0) {
        if (!this.typeStatus) this.typeStatus = true;

        this.typeValue = this.typeArray[this.typeIndex].substring(
          0,
          this.charIndex - 1
        );
        this.charIndex -= 1;
        setTimeout(this.eraseText, this.erasingSpeed);
      } else {
        this.typeStatus = false;
        this.typeIndex += 1;
        if (this.typeIndex >= this.typeArray.length) this.typeIndex = 0;

        setTimeout(this.typeText, this.typingSpeed + 1000);
      }
    },
  },
  created() {
    setTimeout(this.typeText, this.newTextDelay + 200);
  },
};
/* TYPE SCRIPT END*/

</script>


